<template>
  <div class="chat">
    <ul class="list-group">
      <li class="list-group" v-for="line in chatlines" >{{ line.author }}: {{ line.text }}</li>
    </ul>
    <input v-model="username" placeholder="Jane Doe">
    <input v-model="message" placeholder="Say something">
    <button v-on:click="sendMessage" >Send</button>
  </div>
</template>

<script>
export default {
  name: 'Chat',
  props: {
    botname: String
  },
  data () {
    return { 
      chatlines: [
        { 
          author: this.botname,
          text: "Hello"
        },
        {
          author: this.botname,
          text: "How are you?"
        }
      ],
      username: '',
      message: ''
    }
  },
  methods: {
    sendMessage: function (event) {
      this.chatlines.push({ "author": this.username, "text": this.message })
      this.message = ''
    }
  }
}
</script>
  
